.editor {
  margin: 20px auto 20px auto;
  width: 794px;
  height: 1123px;
  background-color: white;
  box-shadow: 0 1px 8px 1px rgba(0, 0, 0, 0.253);
  .ProseMirror {
    overflow: hidden;
    padding-left: 1em;
    padding-right: 1em;
    &:focus {
      outline: unset;
    }
  }
}
